	* {
		margin: 0;
		padding: 0;
		box-sizing: border-box;
	}
	body {
		background-image: linear-gradient(100deg, #b3e7fa 0%, #b840f0 100%);
		color: white;
		font-family: "Poppins", sans-serif;
		min-height: 100vh;

	}
	header {
		font-size: 2rem;
	}
	
	header,
	form {
		min-height: 20vh;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	form input,
	form button {
		padding: 0.5rem;
		font-size: 2rem;
		border: none;
		background: white;
	}
	form button {
		color: #c623eb;
		background: #f7fffe;
		cursor: pointer;
		transition: all 0.3s ease;
	}
	form button:hover {
		background:  #c623eb;
		color: white;
	}
	.no-todos{
		font-size: 2rem;
		min-height: 20vh;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
	.no-todos-picture{
			width: 100px;
			height: 200px;
			background: url('./pictures/4.png') 0 0  no-repeat;
			background-size: contain;
	}
	.todos-time{
		position: absolute;
		right: 20px;
		top: 20px;
	}
	.todo-container {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	
	.todo-list {
		min-width: 30%;
		list-style: none;
	}
	
	.todo {
		margin: 0.5rem;
		background: white;
		font-size: 1.5rem;
		color: black;
		display: flex;
		justify-content: space-between;
		align-items: center;
		white-space: normal;
		transition: all 1s ease;
	}
	.filter-todo {
		padding: 1rem;
	}
	.todo li {
		flex: 1;
	}
	
	.trash-btn,
	.complete-btn {
		background:  #c623eb;
		color: white;
		border: none;
		padding: 1rem;
		cursor: pointer;
		font-size: 1rem;
	}
	.complete-btn {
		background: rgb(11, 212, 162);
	}
	.todo-item {
		padding: 0rem 0.5rem;
	}
	
	.fa-trash,
	.fa-check {
		pointer-events: none;
	}
	
	.fall {
		transform: translateY(10rem) rotateZ(20deg);
		opacity: 0;
	}
	
	.completed {
		text-decoration: line-through;
		opacity: 0.5;
	}
	
	/*CUSTOM SELECTOR */
	select {
		-webkit-appearance: none;
		-moz-appearance: none;
		-ms-appearance: none;
		appearance: none;
		outline: 0;
		box-shadow: none;
		border: 0 !important;
		background-image: none;
	}
	
	/* Custom Select */
	.select {
		margin: 1rem;
		position: relative;
		overflow: hidden;
	}
	select {
		color:  #c623eb;
		font-family: "Poppins", sans-serif;
		cursor: pointer;
		width: 12rem;
	}
	/* Arrow */
	.select::after {
		content: "\25BC";
		position: absolute;
		top: 0;
		right: 0;
		padding: 1rem;
		background:  #c623eb;;
		cursor: pointer;
		pointer-events: none;
	}

	.select:hover::after {
		transform: scale(1.5);
	}
	.container{
		display: flex;

		align-items: center;
		justify-content: center;


	}
	.container-glass{

			background: white;
			min-height: 100vh;
			width: 60%;
			background: linear-gradient(
			to right bottom,
			rgba(255, 255, 255, 0.4),
			rgba(255, 255, 255, 0.3)
			);
			border-radius: 2rem;
			z-index: 2;
			backdrop-filter: blur(2rem);

	}